function MonthlyWork() {
  const [selectedMetrics, setSelectedMetrics] = useState<string[]>([
    '',
    '',
    '',
    '',
    ''
  ]);

  const metrics = [
    { id: '', label: '' },
    { id: '', label: '' },
    { id: '', label: '' },
    { id: '', label: '' },
    { id: '', label: '' }
  ];

  const data = {
    labels: branchesData.map(b => b.name),
    datasets: selectedMetrics.map((metric, index) => ({
      label: metric,
      data: branchesData.map(() => Math.floor(Math.random() * 40) + 60),
      backgroundColor: [
        'rgba(24, 144, 255, 0.7)',
        'rgba(47, 194, 91, 0.7)',
        'rgba(250, 204, 20, 0.7)',
        'rgba(240, 72, 100, 0.7)',
        'rgba(114, 46, 209, 0.7)'
      ][index],
      borderColor: [
        'rgba(24, 144, 255, 1)',
        'rgba(47, 194, 91, 1)',
        'rgba(250, 204, 20, 1)',
        'rgba(240, 72, 100, 1)',
        'rgba(114, 46, 209, 1)'
      ][index],
      borderWidth: 1
    }))
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      y: {
        beginAtZero: true,
        max: 100
      }
    },
    plugins: {
      legend: {
        position: 'top' as const
      }
    }
  };

  const handleMetricChange = (metricId: string) => {
    setSelectedMetrics(prev =>
      prev.includes(metricId)
        ? prev.filter(id => id !== metricId)
        : [...prev, metricId]
    );
  };

  return (
    <div className="monthly-work-card">
      <div className="card-title"></div>
      <div className="metrics-selector">
        {metrics.map(metric => (
          <label key={metric.id} className="metric-checkbox">
            <input
              type="checkbox"
              checked={selectedMetrics.includes(metric.id)}
              onChange={() => handleMetricChange(metric.id)}
            />
            {metric.label}
          </label>
        ))}
      </div>
      <div className="monthly-chart-container">
        <Bar data={data} options={options} />
      </div>
    </div>
  );
}

