function App() {
  const [selectedBranch, setSelectedBranch] = useState(branchesData[0]);
  const [isLoggedIn, setIsLoggedIn] = useState(true);
  const [loginError, setLoginError] = useState('');
  const [isRefreshing, setIsRefreshing] = useState(false);
  const fileInputRef = React.useRef<HTMLInputElement>(null);

  const handleLogin = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const formData = new FormData(e.currentTarget);
    const username = formData.get('username') as string;
    const password = formData.get('password') as string;

    if (username === 'admin' && password === 'bdes@123') {
      setIsLoggedIn(true);
      setLoginError('');
    } else {
      setLoginError('');
    }
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // 
    const fileType = file.name.split('.').pop()?.toLowerCase();
    if (fileType !== 'xlsx' && fileType !== 'xls') {
      alert('Excel.xlsx.xls');
      return;
    }

    // xlsxExcel
    // 
    alert(` "${file.name}" `);
    
    // 
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleDownloadTemplate = () => {
    try {
      // 
      const workbook = utils.book_new();
      
      // 
      Object.entries(excelTemplateData).forEach(([sheetName, data]) => {
        const worksheet = utils.aoa_to_sheet(data);
        utils.book_append_sheet(workbook, worksheet, sheetName);
      });
      
      // Excel
      writeFile(workbook, ".xlsx");
    } catch (error) {
      console.error("Excel", error);
      alert("Excel");
    }
  };

  const handleRefresh = () => {
    setIsRefreshing(true);
    
    // 
    const cards = document.querySelectorAll('.radar-chart-card, .chart-item, .monthly-chart-container');
    
    // 
    cards.forEach((card) => {
      const element = card as HTMLElement;
      element.style.transition = 'opacity 0.5s';
      element.style.opacity = '0.2';
      
      setTimeout(() => {
        element.style.opacity = '1';
      }, 800);
    });
    
    // 
    setTimeout(() => {
      // 
      const monthlyChartEl = document.querySelector('.monthly-chart-container canvas');
      if (monthlyChartEl) {
        const chartInstance = ChartJS.getChart(monthlyChartEl as HTMLCanvasElement);
        if (chartInstance && chartInstance.data && chartInstance.data.datasets) {
          chartInstance.data.datasets.forEach(dataset => {
            if (dataset.data) {
              for (let i = 0; i < dataset.data.length; i++) {
                dataset.data[i] = Math.floor(Math.random() * 40) + 60;
              }
            }
          });
          chartInstance.update();
        }
      }
      
      setIsRefreshing(false);
    }, 1200);
  };

  if (!isLoggedIn) {
    return (
      <div className="login-overlay">
        <div className="login-container">
          <div className="login-title"></div>
          <form className="login-form" onSubmit={handleLogin}>
            <div className="form-group">
              <label htmlFor="username"></label>
              <input
                type="text"
                id="username"
                name="username"
                required
                placeholder=""
              />
            </div>
            <div className="form-group">
              <label htmlFor="password"></label>
              <input
                type="password"
                id="password"
                name="password"
                required
                placeholder=""
              />
            </div>
            {loginError && <div className="error-message">{loginError}</div>}
            <button type="submit" className="login-button">
              
            </button>
          </form>
        </div>
      </div>
    );
  }

  return (
    <div className="layout">
      <div className="header">
        <div className="header-left">
          <img src="gz_logo.png" alt="Logo" className="header-logo" />
        </div>
        <div className="header-content">
          <div className="header-title">
            
          </div>
          <div className="header-subtitle">designed by Liuxing</div>
        </div>
      </div>
      <div className="content">
        <div className="top-section">
          <div className="top-section-header">
            <select
              className="branch-selector"
              onChange={(e) => {
                const branch = branchesData.find(b => b.name === e.target.value);
                if (branch) setSelectedBranch(branch);
              }}
            >
              {branchesData.map((branch, index) => (
                <option key={index} value={branch.name}>
                  {branch.name}
                </option>
              ))}
            </select>
          </div>
          <div className="top-section-content">
            <BasicInfo branch={selectedBranch} />
            <PersonnelAnalysis branch={selectedBranch} />
            <AnnualWork branch={selectedBranch} />
          </div>
        </div>
        <div className="row">
          <MonthlyWork />
        </div>
        <div className="row">
          <BranchAnalysis />
        </div>
        <div className="footer-buttons">
          <input
            type="file"
            ref={fileInputRef}
            className="file-input"
            accept=".xlsx,.xls"
            onChange={handleFileUpload}
            id="fileInput"
          />
          <button 
            className="footer-button upload"
            onClick={() => fileInputRef.current?.click()}
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4"></path>
              <polyline points="17 8 12 3 7 8"></polyline>
              <line x1="12" y1="3" x2="12" y2="15"></line>
            </svg>
            
          </button>
          <button 
            className="footer-button download"
            onClick={handleDownloadTemplate}
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4"></path>
              <polyline points="7 10 12 15 17 10"></polyline>
              <line x1="12" y1="15" x2="12" y2="3"></line>
            </svg>
            
          </button>
          <button 
            className="footer-button refresh"
            onClick={handleRefresh}
            disabled={isRefreshing}
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className={isRefreshing ? 'rotating' : ''}>
              <polyline points="23 4 23 10 17 10"></polyline>
              <polyline points="1 20 1 14 7 14"></polyline>
              <path d="M3.51 9a9 9 0 0 1 14.85-3.36L23 10M1 14l4.64 4.36A9 9 0 0 0 20.49 15"></path>
            </svg>
            {isRefreshing ? '...' : ''}
          </button>
        </div>
      </div>
    </div>
  );
}

export default App;
